import "./Arrows.css";
import { useEffect, useState } from "react";

type ArrowData = {
  disabled: boolean;
  onClick: () => void;
};

const Arrows = (p: {
  back: ArrowData;
  forward: ArrowData;
  onTransitionEnd: () => void;
}) => {
  // Props
  const { back, forward, onTransitionEnd } = p;
  const { disabled: backDisabled, onClick: backOnClick } = back;
  const { disabled: forwardDisabled, onClick: forwardOnClick } = forward;

  // State
  const [backLoaded, setBackLoaded] = useState(false);
  const [forwardLoaded, setForwardLoaded] = useState(false);
  const [backErrored, setBackErrored] = useState(false);
  const [forwardErrored, setForwardErrored] = useState(false);
  const [backTransitionEnded, setBackTransitionEnded] = useState(false);
  const [forwardTransitionEnded, setForwardTransitionEnded] = useState(false);
  const [backPressed, setBackPressed] = useState(false);
  const [forwardPressed, setForwardPressed] = useState(false);

  // Event handlers
  const onErrorBack = () => {
    setBackErrored(true);
    setBackLoaded(true);
  };

  const onErrorForward = () => {
    setForwardErrored(true);
    setForwardLoaded(true);
  };

  const onLoadBack = () => setBackLoaded(true);
  const onLoadForward = () => setForwardLoaded(true);
  const onTransitionEndBack = () => setBackTransitionEnded(true);
  const onTransitionEndForward = () => setForwardTransitionEnded(true);
  const onPointerDownBack = () => setBackPressed(true);
  const onPointerDownForward = () => setForwardPressed(true);

  const onPointerUpBack = () => {
    if (!backDisabled) {
      backOnClick();
    }
    setBackPressed(false);
  };

  const onPointerUpForward = () => {
    if (!forwardDisabled) {
      forwardOnClick();
    }
    setForwardPressed(false);
  };

  // Effects
  useEffect(() => {
    // Complete transition when both arrows have loaded
    if (backTransitionEnded && forwardTransitionEnded) {
      onTransitionEnd();
    }
  }, [backTransitionEnded, forwardTransitionEnded, onTransitionEnd]);

  // UI
  const backArrow = backErrored ? (
    <span>Back</span>
  ) : (
    <img
      alt="Back"
      src="img/arrow.png"
      onLoad={onLoadBack}
      onError={onErrorBack}
    />
  );

  const forwardArrow = forwardErrored ? (
    <span>Forward</span>
  ) : (
    <img
      alt="Forward"
      src="img/arrow.png"
      onLoad={onLoadForward}
      onError={onErrorForward}
    />
  );

  return (
    <div className="Arrows">
      <div
        className={`button back ${backDisabled ? "disabled" : ""} ${
          backPressed ? "pressed" : ""
        }`}
      >
        <div
          className={`img-container ${backLoaded ? "loaded" : ""}`}
          onTransitionEnd={onTransitionEndBack}
          onPointerDown={onPointerDownBack}
          onPointerUp={onPointerUpBack}
        >
          {backArrow}
        </div>
      </div>
      <div
        className={`button forward ${forwardDisabled ? "disabled" : ""} ${
          forwardPressed ? "pressed" : ""
        }`}
      >
        <div
          className={`img-container ${forwardLoaded ? "loaded" : ""}`}
          onTransitionEnd={onTransitionEndForward}
          onPointerDown={onPointerDownForward}
          onPointerUp={onPointerUpForward}
        >
          {forwardArrow}
        </div>
      </div>
    </div>
  );
};

export default Arrows;
